import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './Header.css';
import ContactForm from './ContactForm';

function Header() {
  const location = useLocation();
  
  return (
    <>
      <header className="main-header">
        <div className="header-container">
          <div className="logo">
            <Link to="/" className="logo-link">
              <h1>KNOWNDISC</h1>
            </Link>
          </div>
          <nav className="main-nav">
            <Link to="/" className={`nav-link ${location.pathname === '/' ? 'active' : ''}`}>HOME</Link>
            <Link to="/about" className={`nav-link ${location.pathname === '/about' ? 'active' : ''}`}>ABOUT ME</Link>
            <Link to="/portfolio" className={`nav-link ${location.pathname === '/portfolio' ? 'active' : ''}`}>PORTFOLIO</Link>
            <Link to="/commissions" className={`nav-link ${location.pathname === '/commissions' ? 'active' : ''}`}>COMMISSIONS</Link>
            <Link to="/contact" className={`nav-link ${location.pathname === '/contact' ? 'active' : ''}`}>CONTACT</Link>
          </nav>
        </div>
      </header>
      
      {/* Contact Form Section - Only shown on contact page */}
      {location.pathname === '/contact' && (
        <div className="contact-form-container" style={{
          padding: '80px 20px',
          backgroundColor: '#0a0a0a',
          color: '#fff'
        }}>
          <ContactForm />
        </div>
      )}
    </>
  );
}

export default Header;
